import { FormGroup, FormControl, Validators } from '@angular/forms';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Component, OnInit, Inject } from '@angular/core';
import { regex } from 'src/app/shared/helpers';

@Component({
  selector: 'app-add-user',
  templateUrl: './add-user.component.html',
  styleUrls: ['./add-user.component.scss']
})
export class AddUserComponent implements OnInit {
  formAddUser : FormGroup;
  isSubmit:boolean =  false;
  constructor(
    public dialogRef : MatDialogRef<AddUserComponent>
  ) { }

  ngOnInit(): void {
   this.formAddUser = new FormGroup({
     name: new FormControl('',[Validators.required]),
     email: new FormControl('', [Validators.required, Validators.email]),
     phone: new FormControl('', [Validators.required, Validators.pattern(regex.phone)]),
     gender: new FormControl('',[Validators.required]),
     address: new FormControl('', [Validators.required]),
     area: new FormControl('', [Validators.required])
   })
  }

  closeModal(){
    this.dialogRef.close();
  }
  onSubmit(){
    // if(this.isSubmit){
    //   return false;
    // }
    if(this.formAddUser.invalid){
      return false;
    }
    this.isSubmit = true;
    console.log(this.formAddUser.value);
    if(this.isSubmit){
      this.closeModal();
    }
  }
  get f(){
    return this.formAddUser.controls;
  }
}
